import React, { useEffect } from "react";
import MessengerCustomerChat from "react-messenger-customer-chat";

const ScrollToTop = ({ children }) => {
  const handleScroll = e => {
    const offsetBottom = window.pageYOffset;
    if (
      offsetBottom + document.documentElement.clientHeight >=
      0.8 * document.documentElement.scrollHeight
    ) {
      document.getElementById("scrollTop").style.opacity = "1";
    } else {
      document.getElementById("scrollTop").style.opacity = "0";
    }
  };

  const style = {
    opacity: "0",
    position: "fixed",
    zIndex: 1,
    bottom: 100,
    right: 50
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
  }, []);

  const scrollTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    });
  };

  return (
    <>
      <div>
        <MessengerCustomerChat
          pageId="210834389520299"
          appId="459014251467390"
        />
      </div>
      <div id="scrollTop" onClick={scrollTop} style={style}>
        <i
          className="far fa-arrow-alt-circle-up fa-3x"
          style={{ color: "#ce9b51" }}
        ></i>
      </div>
      {children}
    </>
  );
};

export default ScrollToTop;
