import React, { useEffect } from "react";
import logo from "./logo.svg";

import Home from "./components/Home";
import Footer from "./components/Footer";
import ContactPage from "./components/ContactPage";
import GalleryPage from "./components/GalleryPage";
import RoomsPage from "./components/RoomsPage";
import AboutUsPage from "./components/AboutUsPage";
import BlogsPage from "./components/BlogsPage";

import "./css/Main.css";
import "./css/CustomMain.css";

import * as Scroll from "react-scroll";

//import './css/style.css'
import "./css/customize-controls.css";
import "./css/nivo-slider.css";
import "./css/theme-responsive.css";
import "./css/responsive.css";
import BookingPage from "./components/BookingPage";
import RoomDetailPage from "./components/RoomDetailPage";
import TopHeader from "./components/TopHeader";
import TopBar from "./components/TopBar";
import "./css/hotel-booking.css";
import "./css/NewCss.css";

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { connect } from "react-redux";
import { setRooms as setRoomsAction } from "./redux/actions/room";
import { getFeatureRooms } from "./mockData/config";
import ScrollToTop from "./components/ScrollToTop";
import FacebookMessager from "./components/FacebookMessager";

function App({ setRooms }) {
  useEffect(() => {
    getFeatureRooms().then(item => {
      const data = item.data();
      setRooms(data);
    });
  }, []);

  return (
    <div className="App">
      <ScrollToTop>
        <Router>
          <Switch>
            <Route path="/" exact component={Home} />
            <Route path="/rooms" component={RoomsPage} />
            <Route path="/gallery" component={GalleryPage} />
            <Route path="/blogs" component={BlogsPage} />
            <Route path="/contact" component={ContactPage} />
            <Route path="/roomDetail/:id" component={RoomDetailPage} />
            <Route path="/about" component={AboutUsPage} />
            <Route path="/booking" component={BookingPage} />
          </Switch>
        </Router>
        <Footer />
      </ScrollToTop>
      <FacebookMessager></FacebookMessager>
    </div>
  );
}

export default connect(null, dispatch => ({
  setRooms: data => dispatch(setRoomsAction(data))
}))(App);
